
@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

html {
  background-color: #E7F1CF;
}

h2 {
  margin: 1px;
  padding-bottom: 5px;
}

.startButton {
  padding-left: 120px;
}

.correctAnswer {
  color: green;
}

.incorrectAnswer {
  color: red;
}

button {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  letter-spacing: 2px;
  text-decoration: none;
  text-transform: uppercase;
  color: #000;
  cursor: pointer;
  border: 3px solid;
  padding: 0.25em 0.5em;
  box-shadow: 1px 1px 0px 0px, 2px 2px 0px 0px, 3px 3px 0px 0px, 4px 4px 0px 0px, 5px 5px 0px 0px;
  position: relative;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

button:hover { 
  color: green;
}

button:active {
  color:grey;
}

button:focus {
  color:green;
  box-shadow: none; 
  box-shadow: 1px 1px 0px 0px, 2px 2px 0px 0px;
}

#endPage button {
  margin-left: 20px;
  margin-bottom: 10px;
}

#endPage input {
  border-top-color: black;
}

.multiChoice input[type="checkBox"]{
  display:none;
}

.multiChoice input[type="checkbox"] + label {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 20px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; 
}

.multiChoice input[type="checkbox"]:hover + label:hover {
  color: green;
}

.multiChoice input[type = "checkbox"]:hover + label:before {
  border: 1px solid black;
  box-shadow: 2px 1px 0 black;
}

.multiChoice input[type="checkbox"] + label:before {
  content: "";
  display: block;
  width: 1.4em;
  height: 1.4em; 
  border: 1px solid black;
  border-radius: 1em;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-transition: all 0.2s, background 0.2s ease-in-out;
  transition: all 0.2s, background 0.2s ease-in-out;
  background: #f3f3f3;
  box-shadow: -2px -1px 0 #343a3f;
}

.multiChoice input[type="checkbox"]:checked + label:before {
  border-radius: 1em;
  border: 2px solid #fff;
  width: 1.15em;
  height: 1.15em;
  background: #50565a;
  box-shadow: 2px 1px 0 #50565a;
}

input:focus {
  border-top-color: black;
}

.mainTitle  {
  text-shadow: 3px 3px 1px lightgreen;
  font-size: 55px;
}

.mainLine {
  font-size: 18px;
  width: 500px;
}

.finalScore {
  font-family: 'Press Start 2P';
  padding-bottom: 20px;

}
.scoreBoard {
  font-family: 'Press Start 2P';
  width: 300px;
  list-style-position: inside;
  font-size: 15px;
  padding: 20px;
  word-spacing: 2px;
  border: solid;
  line-height: 20px;
  border-width: 2px;
}


.highScore {
  font-size: 35px;
  font-family: 'Press Start 2P';
  border: solid;
  color: green;
  padding: 10px;
  margin: 10px;
  animation: blinker .75s infinite;
}

@keyframes blinker {
  10% {
    opacity: 0;
  }
}
#scoreNstart {float:left; padding:0px;}

#button {padding: 20px; padding-left: 120px}

#startImage {float:left; padding:0px; padding-left: 50px}

#multChoice {float:left}

#pointKeeping {float:left ;font-size: 20px; text-align: center;
                outline: solid; width: 200px;margin-top: 40px; margin-left: 10px;}

mark.green {
  color: green;
  background: none;
  animation: fadeIn 2s;
  opacity: 75%;
}

@keyframes fadeIn {
  0% {opacity:0}
  100% {opacity:1 }
}

.fireStreak {
  float: right;
  width: 500px;
}

#endPage .fireStreak {
  float: none;
  width: 600px;
}

#points {
  color:green; 
  padding-left: 10px;
  opacity: 75%;
  animation: blinker 1.5s infinite;
}

#round {
  float:left;
}
 #movieMenu {
   align-items: center;
 }

.movieButton {
  float:left;
  padding: 10px;
}